@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: url("../fonts/opensanslight.woff2") format("woff2"), url("../fonts/opensanslight.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
}
.page {
  min-width: 320px;
  background: white;
  color: #283645;
  font-family: "Open Sans", "Arial", sans-serif;
  line-height: 120%;
}
.page-body {
  margin: 0;
}
@media (min-width: 660px) {
  .page {
    min-width: 660px;
  }
}
@media (min-width: 1200px) {
  .page {
    min-width: 1200px;
  }
  .page-main .block-intro-background {
    position: relative;
    min-height: 729px;
    background-image: url("../../img/back-desktop.jpg");
    background-repeat: no-repeat;
    background-position: bottom center;
    background-size: cover;
  }
}
@media (min-width: 1200px) and (min-resolution: 2dppx) {
  .page-main .block-intro-background {
    background-image: url("../../img/back-desktop@2x.jpg");
  }
}
.page-header {
  display: flex;
  position: relative;
  flex-direction: column;
  align-items: center;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.3);
  padding: 0;
  margin: 0;
  z-index: 10;
}
.page-header__logo {
  width: 74px;
  height: 23px;
  background: no-repeat;
}
@media (min-width: 320px) {
  .page-header__logo-desktop,
  .page-header__logo-tablet {
    display: none;
  }
  .page-header__logo-mobile {
    fill: white;
  }
  .page-header__logo-mobile:hover {
    fill: #d22856;
  }
  .page-header__logo-mobile:focus,
  .page-header__logo-mobile:active {
    fill: white;
    opacity: 0.3;
  }
}
@media (min-width: 660px) {
  .page-header__logo {
    position: static;
    width: 145px;
    height: 55px;
    background-repeat: no-repeat;
  }
  .page-header__logo-desktop,
  .page-header__logo-mobile {
    display: none;
  }
  .page-header__logo-tablet {
    display: block;
    fill: white;
  }
  .page-header__logo-tablet:hover {
    fill: #d22856;
  }
  .page-header__logo-tablet:focus,
  .page-header__logo-tablet:active {
    fill: white;
    opacity: 0.3;
  }
}
@media (min-width: 1200px) {
  .page-header__logo {
    position: relative;
    top: -8px;
    width: 150px;
    height: 40px;
    background-repeat: no-repeat;
  }
  .page-header__logo-mobile,
  .page-header__logo-tablet {
    display: none;
  }
  .page-header__logo-desktop {
    display: block;
    fill: white;
  }
  .page-header__logo-desktop:hover {
    fill: #d22856;
  }
  .page-header__logo-desktop:focus,
  .page-header__logo-desktop:active {
    fill: white;
    opacity: 0.3;
  }
}
.main-nav {
  min-width: 320px;
  width: 100%;
  font-weight: bold;
  font-size: 18px;
  line-height: 32px;
}
.main-nav__top {
  display: flex;
  height: 66px;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
  box-shadow: 0 1px 0 black;
}
.main-nav__list {
  color: white;
  list-style-type: none;
  padding: 0;
  margin: 0;
}
.main-nav__dropdown {
  background: none;
  border: none;
  padding: 0;
}
.main-nav__wrapper {
  align-items: center;
}
@media (min-width: 320px) {
  .main-nav {
    position: relative;
    min-height: 51px;
    background-color: transparent;
  }
  .main-nav__dropdown {
    position: absolute;
    top: 20px;
    right: 0;
    z-index: 2;
    display: block;
    width: 70px;
    height: 50px;
    background-color: transparent;
    cursor: pointer;
    border: none;
  }
  .main-nav__dropdown:hover,
  .main-nav__dropdown:active {
    background-color: transparent;
  }
  .main-nav--closed .main-nav__dropdown::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 50px;
    height: 4px;
    background-color: white;
    box-shadow: 0 10px 0 0 white, 0 20px 0 0 white;
  }
  .main-nav--closed .main-nav__dropdown:hover::before {
    background-color: #d22856;
    box-shadow: 0 10px 0 0 #d22856, 0 20px 0 0 #d22856;
  }
  .main-nav--closed .main-nav__dropdown:active::before {
    background-color: #d22856;
    box-shadow: 0 10px 0 0 #d22856 30%, 0 20px 0 0 #d22856 30%;
  }
  .main-nav--opened .main-nav__dropdown {
    top: 0;
    right: 0;
    left: auto;
  }
  .main-nav--opened .main-nav__dropdown::before,
  .main-nav--opened .main-nav__dropdown::after {
    content: "";
    position: absolute;
    top: 33px;
    left: 23px;
    width: 31px;
    height: 3px;
    background-color: white;
  }
  .main-nav--opened .main-nav__dropdown::before {
    transform: rotate(45deg);
    box-shadow: none;
  }
  .main-nav--opened .main-nav__dropdown::after {
    transform: rotate(-45deg);
  }
  .main-nav--opened .main-nav__dropdown:hover::before,
  .main-nav--opened .main-nav__dropdown:hover::after {
    background-color: #d22856;
  }
  .main-nav--opened .main-nav__dropdown:active::before,
  .main-nav--opened .main-nav__dropdown:active::after {
    background-color: #d22856;
    opacity: 0.3;
  }
}
@media (max-width: 1199px) {
  .main-nav--closed .main-nav__list {
    display: none;
  }
  .main-nav--closed .site-list__item {
    display: none;
  }
  .main-nav--nojs .main-nav__dropdown {
    display: none;
  }
  .main-nav--opened .main-nav__top,
  .main-nav--opened .main-nav__wrapper {
    background-color: #283645;
  }
}
@media (min-width: 660px) {
  .main-nav__top {
    height: 110px;
    box-shadow: none;
    background-color: transparent;
  }
  .main-nav--closed .main-nav__dropdown {
    content: "";
    position: absolute;
    top: 43px;
    right: 20px;
    width: 50px;
    height: 24px;
  }
  .main-nav--opened .main-nav__dropdown {
    content: "";
    position: relative;
    top: -1;
    right: 0;
    width: 23px;
    height: 23px;
  }
  .main-nav--opened .main-nav__dropdown::before {
    transform: rotate(45deg);
    box-shadow: none;
  }
  .main-nav--opened .main-nav__dropdown::after {
    transform: rotate(-45deg);
  }
  .main-nav--opened .main-nav__dropdown::before,
  .main-nav--opened .main-nav__dropdown::after {
    top: 10px;
    left: -3px;
    width: 30px;
  }
  .main-nav--catalog {
    margin: 0 auto;
  }
  .main-nav--closed {
    max-width: 660px;
    margin: 0 auto;
  }
  .main-nav--opened {
    background-color: #283645;
  }
  .main-nav--opened .main-nav__top {
    max-width: 620px;
    margin: 0 auto;
  }
}
@media (min-width: 1200px) {
  .main-nav {
    display: flex;
    justify-content: space-between;
    min-width: 1200px;
  }
  .main-nav__wrapper {
    display: flex;
    padding: 0 130px 0 0;
  }
  .main-nav__dropdown {
    display: none;
  }
  .main-nav__top {
    padding: 0 0 0 110px;
    box-shadow: none;
  }
  .main-nav--catalog {
    max-width: 1200px;
    margin: 0 auto;
  }
  .main-nav--form {
    max-width: 1200px;
    margin: 0 auto;
  }
  .main-nav--opened {
    background-color: transparent;
  }
  .main-nav--catalog .main-nav--opened,
  .main-nav--form .main-nav--opened {
    background-color: transparent;
  }
}
.site-list__item {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  box-shadow: 0 1px 0 black, inset 0 1px 0 rgba(255, 255, 255, 0.15);
}
.site-list__link {
  color: white;
  text-decoration: none;
  margin: 0;
  padding: 0;
}
.site-list__link:hover {
  color: #d22856;
}
.site-list__link:active {
  color: white;
}
@media (min-width: 320px) {
  .site-list__desktop {
    text-transform: uppercase;
  }
  .site-list__desktop:hover {
    color: #d22856;
  }
  .site-list__desktop:active {
    color: #d22856;
    opacity: 0.3;
  }
  .site-list__item {
    height: 65px;
    border: none;
  }
}
@media (max-width: 1199px) {
  .site-list__item {
    height: 64px;
  }
}
@media (min-width: 1200px) {
  .site-list__item {
    display: inline-block;
    height: 32px;
    box-shadow: none;
    margin-left: 79px;
  }
  .site-list__link {
    font-weight: normal;
    font-size: 18px;
    line-height: 18px;
  }
  .site-list__mobile {
    display: none;
  }
  .site-list__desktop {
    text-transform: capitalize;
  }
