    color: #d22856;
  }
  .site-list__desktop:active {
    color: white;
    opacity: 0.3;
  }
  .site-list__item--active {
    position: relative;
  }
  .site-list__item--active::after {
    width: 100%;
    height: 50px;
    content: "";
    position: absolute;
    border-bottom: 2px solid white;
    left: 0;
    bottom: -5px;
